import urls from '../constants.js';
import {getBidAskDataWallet} from '../common.js';

export async function getBidTryKoinim() {
  let tryBid =  await getBidAskDataWallet(urls.koinim.general,true);
  return tryBid;
}


export async function getAskTryKoinim() {
  let tryAsk =  await getBidAskDataWallet(urls.koinim.general,false);
  return tryAsk;
};



